<template>
<div>
    <div class="q-title">{{title}}</div>

    <v-list dense>
        <v-list-item-group v-model="selectedItem" v-on:change="selection_changed" color="primary">
            <v-list-item v-for="(item, i) in questionslist" :key="i">
                <v-list-item-icon>
                    <v-icon v-text="icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                    <v-list-item-title v-text="item.q"></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list-item-group>
    </v-list>

    <div class="center">
        <v-btn color="blue" elevation="2" width="150px" rounded @click="gohome">go home
            <v-icon right dark>
                mdi-home
            </v-icon>
        </v-btn>
    </div>
</div>
</template>

<style scoped>
.margin-10 {
    margin-left: 10px;
    margin-right: 10px;
}

.size-x {
    font-size: 0.8rem;
}

.v-btn {
    text-transform: capitalize !important;
}

.q-title {
    height: 80px;
    background: rgb(33 64 131);
    margin: 20px 10px;
    border-radius: 10px;
    padding: 15px;
    color: white;
}

.center {
    text-align: center;
}

.rtitle {
    margin: 25px;
}

.rVideo {
    margin: 10px;
}
</style>

<script>
export default {
    props: ['questionslist'],
    name: 'Search',
    data: () => ({
        selectedItem: undefined,
        icon: "mdi-play-circle-outline",
        title: "What Problems are you facing?",
    }),
    created() {
        console.log(this.questionslist);
    },
    methods: {
        nav_next() {
            this.$emit("next_recording");
        },
        gohome() {
            this.$emit("home");
        },
        selection_changed() {
            if (typeof (this.selectedItem) != 'undefined') {
                this.$emit("select", this.questionslist[this.selectedItem].a);
            }
        }
    }
}
</script>
