<template>
<div>

    <v-img class="logo" :src="require('../assets/logo.png')" />

    <v-form ref="form" class="bottom_login">
        <v-text-field v-model="username" label="Username" required>
            <v-icon slot="prepend">
                mdi-account
            </v-icon>

        </v-text-field>

        <v-text-field v-model="password" label="Password" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" :type="show1 ? 'text' : 'password'" @click:append="show1 = !show1">
            <v-icon slot="prepend">
                mdi-lock
            </v-icon>
        </v-text-field>
        <div class="button_center">
            <v-btn color="blue" elevation="2" rounded @click="doLogin">Login
                <v-icon right dark>
                    mdi-login
                </v-icon>
            </v-btn>
        </div>
    </v-form>
</div>
</template>

<style scoped>
.logo {
    width:200px;
    margin: 15% auto;
}
.bottom_login {
    margin: 20px;
    position: absolute;
    bottom: 30px;
    width: -webkit-fill-available;
}

.button_center {
    text-align: center;
}
</style>

<script>
import {
    mapGetters
} from "vuex";
export default {
    name: 'Login',
    data: () => ({
        show1: false,
        username: '',
        password: ""
    }),
    computed: {
        ...mapGetters(["isAuthenticated"])
    },
    methods: {
        doLogin() {
            this.$store.dispatch("login", {
                user: this.username,
                password: this.password
            });

            if (this.isAuthenticated) {
                this.$router.push({
                    name: "Home"
                });
            }
        }
    }
}
</script>
