<template>
<div class="home">
    <Header class="bottom-margin1" />
    <Selection v-if="!isSelected" v-on:selection="select_Option" />
    <div v-else>
        <div v-if="option == 'mentor'">
          <Mentor v-if="!isQuestion" v-on:mentor_next="mentor_next" :title="user_title" />
          <Question :question_data="qs[counter]" v-on:next_question="next_question" v-else-if="!isRecording" />
          <Recording :recording_input="recording_inputs[counter]" v-on:next_recording="next_recording" v-on:home="goHome" v-if="isRecording" />
        </div>
        <div v-else>
          <Question :question_data="qs[counter]" v-on:next_question="next_question" v-if="!isSearch && !isPlayVideo" />
          <Search v-else-if="!isPlayVideo" :questionslist="search_list" v-on:home="goHome" v-on:select="playVideo" />
          <PlayVideo v-if="isPlayVideo" v-on:close="goHome" v-on:search_next="search_next" :video_url="video_url"/>
        </div>
    </div>
</div>
</template>

<style scoped>
.bottom-margin {
    margin-bottom: 15px;
}
</style>

<script>
// @ is an alias to /src
import Header from '@/components/Header.vue'
import Mentor from '@/components/mentor.vue'
import Question from '@/components/question.vue'
import Recording from '@/components/Recording.vue'
import Selection from '@/components/selection.vue'
import Search from '@/components/search.vue'
import PlayVideo from '@/components/playvideo.vue'

import {
    mapGetters
} from "vuex";

export default {
    name: 'Home',
    components: {
        Mentor,
        Question,
        Recording,
        Header,
        Selection,
        Search,
        PlayVideo
    },
    computed: {
        ...mapGetters(["isAuthenticated", "currentUser"])
    },
    data: () => ({
        isQuestion: false,
        isRecording: false,
        isSelected: false,
        isSearch: false,
        isPlayVideo:false,
        video_url:"",
        option: "",
        user_title: "",
        counter: 0,
        qs: [{
                title: "Today you are going to talk about which machine?",
                type: "select",
                options: [
                    "Lathe Machine",
                    "Wood cutting machine",
                    "Drill Machine",
                ]
            },
            {
                title: "Model of the machine",
                type: "select",
                options: [
                    "GH0987",
                    "KL6790",
                    "RFR-07"
                ]
            }
        ],

        recording_inputs: [{
                title: "What does a three jaw chuck mean?"
            },
            {
                title: "How does it work?"
            }
        ],
        search_list: [
          {
            q:"What does three jaw chuck mean?",
            a:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
          },
          {
            q:"How does it work?",
            a:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
          },
          {
            q:"What is universal chuck?",
            a:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
          },
          {
            q:"How is Horizontal feed is controlled?",
            a:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
          },
          {
            q:"What is apron mechanism?",
            a:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
          }
        ]
    }),
    methods: {
        select_Option(opt) {
            this.option = opt;

            if (opt == 'mentor') {
                this.user_title = "You have entered as a mentor to provide Support Video";
            } else {
                this.user_title = "Welcome to our support video";
            }
            this.isSelected = true;
        },
        mentor_next() {
            this.isQuestion = true;
        },
        next_question() {

            if (this.counter < this.qs.length - 1) {
                this.counter++;
            } else {
                if (this.option == "mentor") {
                    // Start Recording
                    this.isRecording = true;
                } else {
                    this.isSearch = true;
                    this.isQuestion = false;
                }
                this.counter = 0;
            }
        },
        next_recording() {
            if (this.counter < this.recording_inputs.length - 1) {
                this.counter++;
            }
        },
        goHome() {
            this.isRecording = false;
            this.counter = 0;
            this.isSelected = false;
            this.isQuestion = false;
            this.isSearch = false;
            this.isPlayVideo = false;
        },
        playVideo(selected_video) {
          this.isSearch = false;
          this.isPlayVideo = true;
          this.video_url = selected_video;
        },
        search_next() {
          this.isSearch = true;
          this.isPlayVideo = false;
        }
    }
}
</script>
