const state = {
  errors: null,
  user: {},
  userType: '',
  isAuthenticated: false
}

const getters = {
  isAuthenticated (state) {
    return state.isAuthenticated
  },
  currentUser (state) {
    return state.user
  }
}

const actions = {
  login (context, credentials) {
    if (credentials.user == 'user') {
      if (credentials.password == 'password') {
        context.commit('set_auth', { user: { name: 'user' }, type: 'user' })
      }
    }
  },
  logout (context) {
    context.commit('reset_auth')
  }
}

const mutations = {
  set_auth (state, data) {
    state.isAuthenticated = true
    state.user = data.user
    state.errors = {}
    state.userType = data.type
  },
  reset_auth (state) {
    state.isAuthenticated = false
    state.user = {}
    state.errors = {}
    state.userType = ''
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}
