<template>
<div>
    <div class="q-title">{{title}}</div>

    <div class="rtitle"> {{recording_input.title}}</div>
    <div class="center rVideo">
        <video width="320" height="240" v-if="isVideoCaptured" controls>
            <source :src="video_url" type="video/mp4">
            Your browser does not support the video tag.
        </video>
    </div>
    <div width="100%" class="center margin-t-10">
        <v-btn elevation="2" class="bgcolor" v-if="!isCapturing" :disabled="disableCapture" @click="captureVideo"> Capture
            <v-icon right dark>
                mdi-video-outline
            </v-icon>
        </v-btn>
        <v-btn elevation="2" class="bgcolor1" v-else @click="stopCapture"> Stop
            <v-icon right dark>
                mdi-stop
            </v-icon>
        </v-btn>
    </div>
    <v-row>
        <v-col>
            <v-btn plain>View
                <v-icon right dark>
                    mdi-video-box
                </v-icon>
            </v-btn>
        </v-col>
        <v-col>
            <v-btn plain :disabled="!isVideoCaptured" @click="dialog=true">Submit
                <v-icon right dark>
                    mdi-check
                </v-icon>
            </v-btn>
        </v-col>
        <v-col>
            <v-btn plain @click="enableCapture">Retake
                <v-icon right dark>
                    mdi-refresh
                </v-icon>
            </v-btn>
        </v-col>
    </v-row>

    <v-row class="margin-10">
        <v-col>
            <v-btn color="blue" class="size-x" elevation="2" rounded @click="gohome">Back to Home
                <v-icon right dark>
                    mdi-home
                </v-icon>
            </v-btn>
        </v-col>
        <v-col>
            <v-btn color="blue" class="size-x" elevation="2" rounded @click="nav_next">Next question
                <v-icon right dark>
                    mdi-arrow-right-thin
                </v-icon>
            </v-btn>
        </v-col>
    </v-row>

    <v-dialog v-model="dialog" hide-overlay>
        <v-card>
            <v-card-title class="text-h5">
                Video upload
            </v-card-title>
            <v-card-text>
                Video Sucessfully Submitted.
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" text @click="dialog = false">
                    Close
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>
</div>
</template>

<style scoped>
.bgcolor {
    background: rgb(33 64 131) !important;
    color: white;
}

.bgcolor1 {
    background: rgb(180, 2, 2) !important;
    color: white;
}

.margin-10 {
    margin-left: 10px;
    margin-right: 10px;
}

.margin-t-10 {
    margin: 10px 0;
}

.size-x {
    font-size: 0.8rem;
}

.v-btn {
    text-transform: capitalize !important;
}

.q-title {
    height: 80px;
    background: rgb(33 64 131);
    margin: 20px 10px;
    border-radius: 10px;
    padding: 15px;
    color: white;
}

.center {
    text-align: center;
}

.rtitle {
    margin: 25px;
}

.rVideo {
    margin: 10px;
}
</style>

<script>
export default {
    props: ['recording_input'],
    name: 'Question',
    data: () => ({
        title: "Start Recording",
        isCapturing: false,
        dialog: false,
        disableCapture: false,
        isVideoCaptured:false,
        video_url:"http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/ForBiggerJoyrides.mp4"
    }),
    created() {
        console.log(this.recording_input.title);
    },
    methods: {
        nav_next() {
            this.$emit("next_recording");
        },
        gohome() {
            this.$emit("home");
        },
        captureVideo() {
            this.isCapturing = true;
            this.isVideoCaptured=false;
        },
        stopCapture() {
            this.isCapturing = false;
            this.disableCapture = true;
            this.isVideoCaptured=true;
        },
        enableCapture() {
            this.disableCapture = false;
        }
    }
}
</script>
